import firebase from "@firebase/app";
import "@firebase/storage";
import { upload } from "/upload.js";

const firebaseConfig = {
  apiKey: "AIzaSyBTXWFt2I_7jrQqNASXmqiyMZ1gyqi6XFQ",
  authDomain: "fe-upload-213e7.firebaseapp.com",
  projectId: "fe-upload-213e7",
  storageBucket: "fe-upload-213e7.appspot.com",
  messagingSenderId: "223532344888",
  appId: "1:223532344888:web:bf8704e7e52978a2ba28cd",
};
// Initialize Firebase
firebase.initializeApp(firebaseConfig);

const storage = firebase.storage();

upload("#drop", {
  multi: true,
  accept: [".png", ".jpeg", ".jpg", ".gif"],
  onUpload(files, blocks) {
    files.forEach((file, index) => {
      const ref = storage.ref(`images/${file.name}`);
      const task = ref.put(file);

      task.on(
        "state_changed",
        (snapshot) => {
          const percentage =
            ((snapshot.bytesTransferred / snapshot.totalBytes) * 100).toFixed(
              0
            ) + "%";
          const block = blocks[index].querySelector(".preview-info-progress");
          block.textContent = percentage;
          block.style.width = percentage;
        },
        (error) => {
          console.log(error);
        },
        () => {
          task.snapshot.ref.getDownloadURL().then((url) => {
            console.log(`Download url: ${url}`);
          });
        }
      );
    });
  },
});
